.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode td {
  color: #e1e1e1;
}
.dark-mode .anuncio, .dark-mode .resumen-propiedad {
  background-color: #333333;
  border: 1px solid #333333;
  padding: 0 2rem;
}
.dark-mode .anuncio .precio, .dark-mode .resumen-propiedad .precio {
  color: #71B100;
}
.dark-mode .anuncio .icono, .dark-mode .resumen-propiedad .icono {
  filter: invert(100%);
}
.dark-mode .texto-nosotros blockquote {
  color: #FFFFFF;
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #FFFFFF;
}

.no-webp .inicio {
  background-image: url("../img/header.jpg");
}

.webp .inicio {
  background-image: url("../img/header.webp");
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
  margin-bottom: 0;
}
.header {
  background-color: hsl(212, 0%, 0%);
  background-image: radial-gradient(at 51% 61%, hsla(75, 0%, 0%, 0.48) 0px, transparent 50%), radial-gradient(at 20% 78%, hsl(161, 0%, 20%) 0px, transparent 50%), radial-gradient(at 70% 44%, hsla(186, 0%, 0%, 0.83) 0px, transparent 50%);
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header a:hover {
  color: hsl(209, 100%, 47%);
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  padding: 2.5rem 0;
  margin-bottom: 0;
}
.footer {
  background-color: hsl(212, 0%, 0%);
  background-image: radial-gradient(at 51% 61%, hsla(75, 0%, 0%, 0.48) 0px, transparent 50%), radial-gradient(at 20% 78%, hsl(161, 0%, 20%) 0px, transparent 50%), radial-gradient(at 70% 44%, hsla(186, 0%, 0%, 0.83) 0px, transparent 50%);
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .navegacion a:hover {
  color: hsl(209, 100%, 47%);
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
  }
