.barra {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.logo {
  height: 5rem;
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s, linear;
}
