    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
  padding: 2rem;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}
.icono p {
  font-size: 1.5rem;
}

@media (min-width: 768px) {
